const FormDownload = () => {
    return (
        <form className="formTransform">
            <div className="formOne">
                <h3 className="colorOrange font24px fontWeight700">
                    Active Now!
                </h3>
                <p className="colorWhite font16px fontWeight400">想率先掌握最新的數碼媒體營銷數據？</p>
                <h2 className="colorWhite font32px fontWeight900">立即下載報告！</h2>
                <h2 className="colorWhite font32px fontWeight900 marginBottom">為你的決策做好準備！</h2>
                <input type="text" placeholder="稱呼" />
                <input type="text" placeholder="行業" />
                <input type="text" placeholder="職位" />
                <input type="text" placeholder="Email" />
                <div className="newInfoDiv">
                    <input type="checkbox" id="newInfo"/><label htmlFor="newInfo" className="checkbox"></label>
                    <label htmlFor="newInfo" className="colorWhite">我想收到Tech on Fire的最新資訊</label>
                </div>
                <div className="classInfoDiv">
                    <input type="checkbox" id="clsssInfo" /><label htmlFor="classInfo" className="checkbox"></label>
                    <label htmlFor="classInfo" className="colorWhite">有興趣了解Tech on Fire課程資訊</label>
                </div>

                <div className="colorWhite middleBtn fontWeight900">下載報告</div>
            </div>
        </form>
    );
};

export default FormDownload;
